.getStartedButton{
    background-color: #07137C;
    color: #fff;
    border-radius: 20px;
    width: 8rem;
    border: 1px solid #07137C;
    margin-left: 10px;
    -webkit-box-shadow:0px 8px 15px rgba(0, 15, 91, 0.396607);
    -moz-box-shadow: 0px 8px 15px rgba(0, 15, 91, 0.396607);
    box-shadow: 0px 8px 15px rgba(0, 15, 91, 0.396607)
}

  a {
    position: relative;
    color: #000;
    text-decoration: none;
  }
  
  a:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    bottom: -20px;
    left: 0;
    background-color: #07137C;
    visibility: hidden;
    -webkit-transform: scaleX(0);
    transform: scaleX(0);
    -webkit-transition: all 0.3s ease-in-out 0s;
    transition: all 0.3s ease-in-out 0s;
  }
  
  .black.active a:before{
    visibility: visible;
    -webkit-transform: scaleX(1);
    transform: scaleX(1);
  }